/* ------------Global CSS -----------------*/
* {
    font-family: 'Poppins', sans-serif;
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}

html {
    scroll-behavior: smooth;
}

body {
    color: white;
}

.container {
    padding: 10px 10%;
}

.logo-btn img {
    width: 100px;
}

.logo-btn {
    margin: 0 5%;
    cursor: pointer;
}

.header-section-login {
    width: 100%;
    height: 100vh;
    background-size: cover;
    background-position: center;
    background-image: linear-gradient(rgba(4, 9, 30, 0.141), rgba(4, 9, 30, 0.7)), url(../Login/Images/login-bg.jpg);
    position: relative;
}

.header-text p {
    margin: 20px 0;
    font-size: 20px;
}

/* --------Login--------- */
.login, .register {
    display: flex;
    justify-content: center;
    align-items: center;
    padding-top: 5%;
    width: 100%; 
    height: 100vh; 
    margin: auto;
}

.wrapper {
    background: rgba(2, 4, 12, 0.544);
    backdrop-filter: blur(1px);
    width: 35%;
    min-height: 65vh;
    border-radius: 20px;
    padding: 30px 40px;
    margin: auto;
}

.wrapper h1 {
    font-size: 36px;
    text-align: center;
    color: white;
}

.wrapper .input-box {
    position: relative;
    width: 100%;
    height: 50px;
    margin: 30px 0;
}

.input-box input {
    width: 100%;
    height: 100%;
    border: none;
    outline: none;
    background: transparent;
    border: 1px solid;
    font-size: 16px;
    padding: 20px;
}

.input-box input::placeholder {
    color: white;
}

.input-box i {
    position: absolute;
    right: 10%;
    top: 50%;
    transform: translateY(-50%);
    font-size: 20px;
}

.input-bar {
    color: white;
    border-radius: 10px;
}

.wrapper .rem-for {
    display: flex;
    justify-content: space-between;
    align-items: center;
    font-size: 14px;
    margin: -15px 0 15px;
}

.fa-solid {
    color: white;
}

#toggle-pass,
#hide-pass,
#register-toggle-pass,
#register-hide-pass {
    cursor: pointer;
}

.rem-for label input {
    margin-right: 3px;
    justify-content: center;
}

.checkbox-label {
    display: flex;
    align-items: center;
    margin: 15px 0;
}

.rem-for a {
    color: white;
    text-decoration: none;
}

.rem-for a:hover {
    text-decoration: underline;
}

.wrapper .btn {
    width: 100%;
    height: 45px;
    background: white;
    border-radius: 10px;
    border: none;
    outline: none;
    font-size: 16px;
    cursor: pointer;
    font-weight: 500;
}

.wrapper,
.reg-link,
.log-link {
    font-size: 14px;
    text-align: center;
    margin-top: 20px 0 15px;
}

.reg-link p a {
    color: white;
    text-decoration: none;
    font-weight: 500;
}

.reg-link p {
    margin-top: 30px;
}

.reg-link p a:hover {
    text-decoration: underline;
}

/****Register form****/
.log-link p a {
    color: white;
    text-decoration: none;
    font-weight: 500;
}

.log-link p {
    margin-top: 30px;
}

.log-link p a:hover {
    text-decoration: underline;
}

input[type="password"]::-ms-reveal {
    display: none;
}

input[type="password"]::-ms-clear {
    display: none;
}

input[type="password"] {
    appearance: none;
    -webkit-appearance: none;
    -moz-appearance: none;
}

/* --------Fix for Register Form Centering--------- */
.register {
    visibility: visible; /* Ensure form is always part of the DOM */
    opacity: 1; /* Ensure form is fully visible when active */
    transition: opacity 0.5s ease, visibility 0s ease 0.5s; /* Fade transition */
}

.register.hidden {
    opacity: 0;
    visibility: hidden;
    transition: opacity 0.5s ease, visibility 0s ease 0s; /* Fade out transition */
}
